.types{
  font-family: pokemontype;
  font-size: 14px;
  padding-top: 5px;
  width: 83px;
  height: 18px;
  display: inline-block;
  text-transform: uppercase;
  color: white;
  text-shadow: 2px 2px #303030;
  overflow-x: hidden;
  border-radius: 5px;
  margin-top: -5px;
  margin-left: 10px;
  text-align: center;
  cursor: pointer;
}
.types_small{
    font-family: pokemontype;
    font-size: 10px;
    padding-top: 5px;
    width: 63px;
    height: 14px;
    display: inline-block;
    text-transform: uppercase;
    color: white;
    text-shadow: 1px 1px #303030;
    overflow-x: hidden;
    border-radius: 5px;
  }
  .types_small:hover{
    border-bottom-style: solid;
    border-bottom-color: rgb(255, 80, 80);
    border-spacing: 15px;
  }
  .fire{
    background: #ff5430;
    border:3px solid #ba3710;
  
  }
  .normal{
