 input[type="range"] { 
    -webkit-appearance: none;
    position: relative;
    overflow: hidden;
    height: 20px;
    width: 200px;
    border-radius: 10px;
    left: 60px;
    top: -22px;
  }
  ::-webkit-slider-runnable-track {
    background: rgb(190, 190, 190);
  }
  ::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 20px; 
    height: 20px;
    margin-left: 10px;
    border-radius: 15px;
    margin-left: -2px;
  }
  
